//Promise
let p1 = new Promise((resolve, reject) => {
    setTimeout(function () {
        resolve(111)
    }, Math.random() * 1000)
})
let p2 = new Promise((resolve, reject) => {
    setTimeout(function () {
        resolve(222)
    }, Math.random() * 1000)
})
Promise.all([p1, p2]).then(data => {
    console.log(data) //  [111, 222]
}, err => {
    console.log(err)
})

Promise.race([p1, p2]).then(data => {
    console.log(data) // 111 或者 222 看哪个先执行完
}, err => {
    console.log(err)
})
// yield
function* fn() {
    console.log('aaa')
    yield console.log('bbb')
    yield console.log('ccc')
    yield getData()
}
function getData() {
    setTimeout(() => {
        console.log('xxx')
        f.next()
    }, 1000)
}
let f = fn()
// f.next() // aaa bbb 
// f.next()  //ccc

