class Clock extends React.Component {
    constructor(props) {
        super(props)
        console.log(props)
    }
    render() {
        return <div>
            {this.props.flag}
        </div>
    }



}
class Test extends React.Component {
    state = { x: 0 }
    render() {
        return <div>
            <Clock flag={this.state.x} />
            <button onClick={_ => {
                let x = this.state.x
                this.setState({ x: x + 1 })
            }}>click me</button>
        </div>
    }
}

ReactDOM.render(<div>
    <Test />
</div>, document.getElementById('root'))

